* {
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

button {
  font-family: Arial, Helvetica, sans-serif;
}

audio {
  width: 100%;
}

ul {
  list-style: none;
  padding: 0;
}

audio {
  margin-bottom: 0;
}
